body {
  margin: 0;
  padding: 0;
  font-family: 'PT Serif', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
@media only screen and (min-width: 768px) {
  .col-md-6 {
    width: 50%;
  }
}
.text-right {
  text-align: right;
}
.pad-right {
  padding-right: 20px;
}
.text-transform-none {
  text-transform: none !important;
}
h1 {
  margin: 0 0 10px;
  color: #b33320;
}
h1 small {
  font-size: 50%;
}
.image-section {
  text-align: center;
}
.image-section canvas {
  margin-top: 15px;
}
.card {
  margin-bottom: 15px;
}
.saved-units {
  margin-top: 15px;
}
.list-group-item {
  padding: 5px 10px;
  line-height: 30px;
  overflow: hidden;
}

.unit-card {
  border: 7px solid #b33320;
  width: 320px;
  max-width: 100%;
  margin: 15px auto;
  text-align: left;
  position: relative;
}
.card-corner-ne {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 40px;
}
.card-corner-se {
  position: absolute;
  bottom: 5px;
  right: 5px;
  width: 40px;
}
.card-corner-sw {
  position: absolute;
  bottom: 5px;
  left: 5px;
  width: 40px;
}
.card-inner {
  box-shadow: inset 0px 0px 5px 5px rgba(0, 0, 0, 0.25);
  border: 2px solid black;
  width: 100%;
  min-height: 200px;
  padding: 13px 28px;
  position: relative;
  color: #b33320;
  font-size: 13px;
}
.card-top {
  padding-left: 70px;
  padding-top: 10px;
  min-height: 90px;
}
.card-top-fort {
  min-height: 75px;
}
.card-main {
  padding-bottom: 15px;
}
.card-name {
  font-family: 'Domine', serif;
  text-transform: uppercase;
  font-weight: 700;
  color: #df1314;
  font-size: 16px;
  border-bottom: 2px solid black;
  margin-bottom: 5px;
}
.card-type {
  font-family: 'Domine', serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
}
.card-traits {
  font-family: 'Domine', serif;
  text-transform: uppercase;
  font-weight: 700;
  color: #df1314;
  margin-top: 7px;
}
.card-table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
  border-top: 2px solid black;
}
.card-table td {
  border-bottom: 2px solid black;
  color: #df1314;
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'Cabin', sans-serif;
}
.card-cost {
  text-align: right;
  text-transform: uppercase;
}
.commander {
  margin-top: 10px;
  text-align: center;
  font-style: italic;
}

.card-flags {
  position: absolute;
  top: -9px;
  left: 15px;
}
.card-flag {
  width: 30px;
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin-right: 5px;
}
.fort-flag {
  margin-top: 25px;
  width: 70px;
}
.fort-flag img {
  max-width: 100%;
}
.experience-flag,
.ancestry-flag,
.type-flag {
  width: 30px;
}
.experience-flag {
  position: absolute;
  top: 74px;
  left: 0;
}
.type-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: -10px;
}
.card-flag svg {
  width: 100%;
  height: auto;
}

.stat-form {
  text-align: left;
}
.trait-description {
  font-size: 10px;
}

.collapse {
  border: none;
  padding: 0;
  clear: both;
  float: left;
  width: 100%;
}
.collapse legend {
  padding: 5px;
  font-size: 13px;
  font-size: 80%;
}

